import React, { useEffect, useState } from 'react';
import { SUCCESS } from '../../../store/callApi/actions';
import config from '../../../config';
import { useHistory, useLocation } from 'react-router-dom';
import { Waypoint } from 'react-waypoint';
import { createCategoryHashtag, defaultTrainer, loadingSmall } from '../../../extensions';
import Frame from './Frame';
import { HBButtonSmall, HBModalConfirm, HBSearch } from '../../../hbBaseClass';
import styles from './MobilitySaved.module.scss';
import Saly24 from 'assets/images/Saly-24.png';
import { getPageSize, intToString } from '../../../utils';
import Tippy from '@tippyjs/react';
import 'tippy.js/dist/tippy.css';
import { useApi } from 'store/callApi';
import { KMSDiv } from 'components';
import { pluralizeExcercise } from 'utilities';

interface Props {
  location: { search: string };
}

const DAY = 3600 * 24;
const WEEK = DAY * 7;
const MONTH = DAY * 30;
const YEAR = MONTH * 12;

const MobilitySaved: React.FC<Props> = () => {
  const location = useLocation();
  let params: any = location.search;
  const query = new URLSearchParams(params);
  const isMine = query.get('history') || '';
  const { callApi } = useApi();
  const history = useHistory();
  const [videos, setVideos] = useState<any>({ data: [], loading: true, full: false, page: 0 });
  const [searchName, setSearchName] = useState('');
  const [isVisiblePopupClearAll, setIsVisiblePopupClearAll] = useState(false);
  const [checkLoading, setCheckLoading] = useState(false);

  const nameId = isMine ? 'watch_history_id' : 'id';

  /**
   * Filter loaded videos to avoid data has video duplicate id
   * @param oldVideos Videos data before call api
   * @param newVideos Video data from api
   *
   * @returns list videos without duplicate id with list old videos
   */
  const filterDuplicates = (oldVideos: any[], newVideos: any[]) => {
    return newVideos.filter((item: any) => oldVideos.findIndex((item2: any) => item2[nameId] === item[nameId]) === -1);
  };

  const getAllSavedVideos = (searchName: string, page: number) => {
    const pageSize = getPageSize(page);

    callApi(
      {
        method: 'get',
        api: !isMine
          ? config.rest.getSearchVideos('1', searchName, '', pageSize, page)
          : config.rest.getHistoryWatch(searchName, page, pageSize),
        loading: page === 1,
      },
      (response) => {
        const { data, status } = response;
        if (status === SUCCESS) {
          setVideos((videos: any) => {
            return {
              data: page === 1 ? data.videos : [...videos.data, ...filterDuplicates(videos.data, data.videos)],
              full: data.videos.length < pageSize,
              loading: false,
              page: page,
            };
          });
        }
      },
    );
  };

  const savedOrUnSaved = (id: any, isSaved: any) => {
    setVideos((videos: any) => {
      let newVideos = [...videos.data];
      const index = newVideos.findIndex((item: any) => item.id === id);
      newVideos[index].is_saved = !newVideos[index].is_saved;
      return { ...videos, data: newVideos };
    });
    callApi(
      {
        method: !isSaved ? 'post' : 'put',
        api: !isSaved ? config.rest.saveVideo(id) : config.rest.unsaveVideo(id),
      },
      (response) => {
        const { status } = response;
        if (status === SUCCESS) {
        }
      },
    );
  };

  const unsaveAllMobility = () => {
    callApi(
      {
        method: 'delete',
        api: !isMine ? config.rest.unsaveALLMobility() : config.rest.clearAllHistoryWatch(),
        loading: true,
      },
      (response) => {
        const { status } = response;
        if (status === SUCCESS) {
          setIsVisiblePopupClearAll(false);
          setVideos({ ...videos, data: [] });
        }
      },
    );
  };

  const onCancelClearAll = () => {
    setIsVisiblePopupClearAll(false);
  };

  const removeVideoHistory = (videoId: any) => {
    let newHistoriesWatch = { ...videos };
    let arrayVideo = [...videos?.data];
    arrayVideo = arrayVideo.filter((item: any) => item?.watch_history_id !== videoId);
    newHistoriesWatch.data = arrayVideo;
    setVideos(newHistoriesWatch);

    callApi(
      {
        method: 'delete',
        api: config.rest.removeHistoryWatch(videoId),
      },
      (response: any) => {
        const { status } = response;
        console.log('response', response);
        if (status === SUCCESS) {
        }
      },
    );
  };

  useEffect(() => {
    getAllSavedVideos(searchName, 1);
    window?.scrollTo(0, 0);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const loadMoreVideos = () => {
    if (videos.loading || videos.full) return;
    setVideos({ ...videos, loading: true });
    getAllSavedVideos(searchName, videos.page + 1);
  };

  const searchParams: any = {
    className: 'header-topic--wrapper__search--selection__world',
    searchName,
    setSearchName,
    searchEmpty: true,
    callbackSearch: (searchValue: string) => {
      setCheckLoading(true);
      getAllSavedVideos(searchValue, 1);
    },
    isNew: true,
    clearBtn: true,
    onKeyPress: (searcValue: string) => {
      getAllSavedVideos(searcValue, 1);
    },
    timeoutSearch: 0,
    callbackEmpty: () => {
      setCheckLoading(true);
      getAllSavedVideos('', 1);
    },
  };

  // console.log('QUY', isSearch);
  const renderItem = (video: any) => {
    return (
      <div className="hb-card-item" key={isMine ? video?.watch_history_id : video?.id}>
        <div
          className={styles.itemContent}
          style={{
            backgroundColor: 'white',
            padding: 15,
            borderRadius: 32,
            height: 181,
            display: 'flex',
            alignItems: 'center',
          }}
        >
          <div>
            <KMSDiv
              backgroundImage={video?.thumbnail}
              className={styles.thumbnail}
              style={{
                overflow: 'hidden',
                display: 'block',
                borderRadius: 20,
                width: 150,
                height: 150,
                backgroundSize: 'cover',
                marginRight: 20,
                cursor: 'pointer',
              }}
              onClick={() => history.push(`/mobility/watch?video=${video?.id}`)}
            />
          </div>
          <div
            className={styles.itemInfo}
            style={{
              marginRight: 20,
              width: '100%',
              height: '100%',
              display: 'flex',
              flexDirection: 'column',
              justifyContent: 'space-between',
              flex: 1,
            }}
          >
            <div>
              <div className="d-flex justify-content-between">
                <div className={`${styles.informationTrainer} d-flex justify-content-between`}>
                  <KMSDiv
                    backgroundImage={video?.trainer_avatar || defaultTrainer}
                    className={styles.avatar}
                    style={{
                      borderRadius: '50%',
                      width: 32,
                      height: 32,
                      backgroundSize: 'cover',
                      backgroundPosition: 'center',
                      backgroundRepeat: 'no-repeat',
                      cursor: video?.trainer_id ? 'pointer' : 'default',
                    }}
                    onClick={() => {
                      if (video?.trainer_id) history.push(`/mobility/trainer-detail/${video?.trainer_id}`);
                    }}
                  />
                  <Tippy interactive content={video?.viewed_amount || '0'}>
                    <div className={styles.amountView}>
                      <i className={'hb-icon-eye'} />
                      <span>{intToString(video?.viewed_amount)}</span>
                    </div>
                  </Tippy>
                </div>
                <div
                  className={`${isMine ? styles.hbSave : 'hb-save'}`}
                  onClick={() =>
                    isMine ? removeVideoHistory(video.watch_history_id) : savedOrUnSaved(video.id, video.is_saved)
                  }
                >
                  {isMine ? (
                    <i className={`${styles.iconCross} hb-icon-cross`} />
                  ) : (
                    <i className={`${video.is_saved ? 'hb-icon-heart-s' : 'hb-icon-heart-r'}`} />
                  )}
                </div>
              </div>

              <div className={styles.content} onClick={() => history.push(`/mobility/watch?video=${video?.id}`)}>
                {video?.title.length > 30 ? video?.title.slice(0, 27) + '...' : video?.title}
              </div>
            </div>
            <div
              className={`d-flex ${styles.tags}`}
              style={{ cursor: 'pointer' }}
              onClick={() => history.push(`/mobility/watch?video=${video?.id}`)}
            >
              {createCategoryHashtag(video.category_name)}
            </div>
          </div>
        </div>
      </div>
    );
  };

  let group: { label: string; arr: any[]; count: number }[] = [];
  const addGroup2 = (label: string, item: any) => {
    const index = group.findIndex((item: any) => item.label === label);
    if (index === -1) group.push({ label, arr: [item], count: isMine ? 1 : item.is_saved ? 1 : 0 });
    else {
      group[index].arr.push(item);
      group[index].count += isMine ? 1 : item.is_saved ? 1 : 0;
    }
  };

  const today = new Date();
  const now = today.getTime();
  const todaySeconds = today.getHours() * 3600 + today.getMinutes() * 60 + today.getSeconds();

  const addGroup = (item: any) => {
    const date = new Date((item.saved_date || item.created_date) * 1000);
    let different = (now - date.getTime()) / 1000;
    if (different < todaySeconds) {
      addGroup2('Heute', item);
      return;
    }
    different += 86399 - todaySeconds;
    if (different < DAY * 2) addGroup2('1 Tag', item);
    else if (different < WEEK) addGroup2(Math.floor(different / DAY) + ' Tage', item);
    else if (different < WEEK * 2) addGroup2('1 Woche', item);
    else if (different < MONTH) addGroup2(Math.floor(different / WEEK) + ' Wochen', item);
    else if (different < MONTH * 2) addGroup2('1 Monat', item);
    else if (different < YEAR) addGroup2(Math.floor(different / MONTH) + ' Monate', item);
    else if (different < YEAR * 2) addGroup2('1 Jahr', item);
    else addGroup2(Math.floor(different / YEAR) + ' Jahre', item);
  };
  videos.data.forEach((item: any) => addGroup(item));

  if (group.length === 0 && !searchName && videos.loading) return <div />;

  return (
    <>
      <HBModalConfirm
        show={isVisiblePopupClearAll}
        handleClose={onCancelClearAll}
        title={isMine ? 'Gesehene löschen' : 'Gespeicherte löschen'}
        content={
          isMine
            ? 'Sind Sie sicher, dass Sie Ihre gesehenen Übungen vollständig löschen möchten?'
            : 'Sind Sie sicher, dass Sie Ihre gespeicherten Übungen vollständig löschen möchten?'
        }
        up="Ja, löschen"
        upCallback={unsaveAllMobility}
        down="Abbrechen"
        downCallback={onCancelClearAll}
      />
      <div className="hb-mx-20">
        <div className="d-flex justify-content-between hb-my-28">
          <HBButtonSmall className={styles.btn} onClick={() => history.goBack()}>
            <i className="hb-icon-arrow-left hb-icon-md" /> Zurück
          </HBButtonSmall>
          {group.length !== 0 && (
            <HBButtonSmall
              mini
              onClick={() => {
                setIsVisiblePopupClearAll(true);
                setCheckLoading(false);
              }}
            >
              Alle löschen
            </HBButtonSmall>
          )}
        </div>
        <h1 className={styles.title}>{!isMine ? ' Gespeicherte Übungen' : 'Angesehene Übungen'}</h1>
      </div>

      {group.length === 0 && !checkLoading && !searchName && !videos.loading ? (
        <div className="hb-no-content">
          <div className="hb-no-content__image">
            <div className="image-content-wrap">
              <h2 className="image-content-title">{`Keine ${isMine ? 'Angesehene' : 'Gespeicherten'} Übungen`}</h2>
              {!isMine && (
                <>
                  <h4 className="image-content">Um Übungen zu speichern, tippen</h4>{' '}
                  <h4 className="image-content-below">Sie auf das Herz beim Übung.</h4>
                </>
              )}
            </div>
            <div style={{ backgroundImage: `url("${Saly24}")` }} className="image-saly" />
          </div>
        </div>
      ) : (
        <div className={`hb-card ${styles.wrapperContainer}`}>
          <div className="videos-saved--page">
            <div className="row">
              <div className="col-md-12">
                <HBSearch placeholder="Übung suchen" {...searchParams} nightBlue />
              </div>
            </div>
            {group.map((item: any) => (
              <div className="levelDateWrapper" key={item.label}>
                <div className="d-flex justify-content-between">
                  <h2 className="levelDateWrapperTitle">{item.label}</h2>
                  <div className="levelDateWrapperAmountVideos">{pluralizeExcercise(item.count)}</div>
                </div>
                <div className={styles.mobilityWrapper}>
                  {item.arr.map((video: any) => {
                    return renderItem(video);
                  })}
                </div>
              </div>
            ))}

            {group.length === 0 && (
              <div className="row">
                <p className={styles.noVideo}>Keine Suchergebnisse</p>
              </div>
            )}
            {videos.page !== 0 && videos.loading && <Frame size="large" render={loadingSmall} />}
            <Waypoint onEnter={loadMoreVideos} />
          </div>
        </div>
      )}
    </>
  );
};

export default MobilitySaved;
